
.item {
    width: 200px;
    /* background-color: #fde1ff; */
    /* border: .05px solid grey; */
    box-shadow: 0 10px 15px #fde1ff;
    padding: 15px;
    border-radius: 5px;
}

.item img {
    width: 100%;
}

.item p {
    margin: 6px 0px;
}

.item-prices {
    display: flex;
    gap: 20px;
}

.item-price-new {
    color: #374151;
    font-size: 18px;
    font-weight: 600;
}

.item-price-old {
    color: #8c8c8c;
    font-size: 18px;
    font-weight: 500;
    text-decoration: line-through;
}

.item:hover {
    transform: scale(1.05);
    transition: all .6s ease;
}


/* WEB BREAKPOINTS */
@media (max-width: 1440px) {
    .item, .item-price-old, .item-price-new {
        font-size: 14px;
    }
}

@media (max-width: 1024px) {
    .item {
        width: 170px;
        font-size: 13px;
    }

    .item-price-old, .item-price-new {
        font-size: 13px;
    }
}

@media (max-width: 834px) {
    .item {
        width: 120px;
        font-size: 12px;
    }

    .item-price-new, .item-price-old {
        font-size: 12px;
    }
}

@media (max-width: 480px) {
    .item {
        width: 160px;
        font-size: 11px;
    }

    .item-price-new, .item-price-old {
        font-size: 11px;
    }
} 
