.footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 50px;
    padding-top: 30px;
}

.footer-logo {
    display: flex;
    align-items: center;
    gap: 20px;
}

.footer-logo p {
    color: #383838;
    font-size: 30px;
    font-weight: 700;
}

.footer-logo img {
    height: 40px;
}

.footer-links {
    display: flex;
    list-style: none;
    gap: 50px;
    color: #252525;
    font-size: 20px;
}

.footer-links li {
    cursor: pointer;
}

.footer-social-icon {
    display: flex;
    gap: 20px;
}

.footer-icons-container {
    padding: 10px;
    padding-bottom: 6px;
    background: #fbfbfb;
    border: 1px solid #ebebeb;
}

.social {
    font-size: 30px;
}

.footer-copyright {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    width: 100%;
    margin-bottom: 30px;
    color: #1a1a1a;
    font-size: 20px;
}

.footer-copyright hr {
    width: 80%;
    border: none;
    border-radius: 10px;
    height: 3px;
    background: #c7c7c7;
}

@media (max-width: 834px) {
    .footer {
        gap: 20px;
    }

    .footer-logo {
        gap: 10px;
        align-items: end;
    }

    .footer-logo img {
        width: 40px;
    }

    .footer-logo p {
        font-size: 25px;
    }

    .footer-links {
        font-size: 14px;
        gap: 10px;
    }

    .social {
        font-size: 20px;
    }

    .footer-copyright {
        font-size: 13px;
    }
}

@media (max-width: 480px) {
    .footer {
        gap: 10px;
    }

    .footer-logo {
        gap: 5px;
    }

    .footer-logo img {
        width: 30px;
    }

    .footer-logo p {
        font-size: 18px;
    }

    .footer-links {
        font-size: 10px;
        gap: 4px;
        justify-content: center;
        padding: 0;
    }

    .social {
        font-size: 15px;
    }

    .footer-copyright {
        font-size: 12px;
    }
}