.shop-category {
    margin-bottom: 90px;
}

.shopCategory-banner {
    display: block;
    margin: 30px auto;
    width: 60%;
}

.shopCategory-indexSort {
    display: flex;
    margin: 0px 170px;
    justify-content: space-between;
    align-items: center;
}

.shopCategory-indexSort p span {
    font-weight: 600;
}

.shopCategory-sort {
    padding: 10px 20px;
    border-radius: 40px;
    border: 1px solid #888;
}

.shopCategory-products {
    margin-top: 50px;
    display: flex;
    gap: 30px;
    flex-wrap: wrap;
    justify-content: center;
}

.shopCategory-loadmore {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 150px auto;
    width: 233px;
    height: 69px;
    border-radius: 75px;
    background: #ededed;
    color: #787878;
    font-size: 18px;
    font-weight: 500;
}



/* WEB BREAKPOINTS */
@media (max-width: 1440px) {
    .shopCategory-banner {
        margin: 20px auto;
        width: 90%;
    }

    .shopCategory-indexSort {
        width: 90%;
        margin: auto;
    }

    .shopCategory-sort {
        padding: 5px 10px;
        font-size: 12px;
    }

    .shopCategory-indexSort p {
        font-size: 12px;
    }

    .shopCategory-products {
        width: 90%;
        margin: 10px auto;
        row-gap: 40px;
    }

    .shopCategory-loadmore {
        margin: 100px auto;
        width: 200px;
        height: 60px;
        font-size: 16px;
    }
}

@media (max-width: 834px) {
    .shopCategory-banner {
        width: 95%;
    }

    .shopCategory-indexSort {
        width: 95%;
    }

    .shopCategory-products {
        width: 95%;
    }

    .shopCategory-loadmore {
        margin: 60px auto;
        width: 150px;
        height: 40px;
        font-size: 13px;
    }
}