.offers {
    width: 65%;
    height: 400px;
    display: flex;
    margin: auto;
    padding: 0px 140px;
    margin-top: 150px;
    margin-bottom: 150px;
    background: linear-gradient(180deg, #fde1ff 0%, #e1ffea22 60%);
    overflow-y: hidden;
}

.offers-left {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.offers-left h1 {
    color: #171717;
    font-size: 60px;
    font-weight: 600;
}

.offers-left p {
    color: #171717;
    font-size: 20px;
    font-weight: 600;
}

.offers-left button {
    width: 202px;
    height: 50px;
    border-radius: 35px;
    background: #ff4141;
    border: none;
    color: white;
    font-size: 22px;
    font-weight: 500;
    margin-top: 30px;
    cursor: pointer;
}

.offers-right {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-top: 50px;
}

.offers-right img {
    position: relative;
    top: 78px;
}


/* WEB BREAKPOINTS */
@media (max-width: 1440px) {
    .offers {
        padding: 0 80px;
        margin-bottom: 120px;
        height: 300px;
    }

    .offers-left h1 {
        font-size: 40px;
    }

    .offers-left p {
        font-size: 18px;
    }

    .offers-left button {
        width: 135px;
        height: 45px; 
        font-size: 18px;
    }

    .offers-right {
        padding-top: 30px;
    }

    .offers-right img {
        width: auto;
        height: 500px;
    }
}

@media (max-width: 1024px) {
    .offers {
        padding: 0 60px;
        margin-bottom: 80px;
        height: 250px;
    }

    .offers-left h1 {
        font-size: 30px;
    }

    .offers-left p {
        font-size: 16px;
    }

    .offers-left button {
        width: 125px;
        height: 40px; 
        font-size: 16px;
    }

    .offers-right {
        padding-top: 10px;
    }

    .offers-right img {
        width: auto;
        top: 60px;
        height: 400px;
    }
}

@media (max-width: 834px) {
    .offers {
        padding: 10px 50px;
        margin-bottom: 60px;
        height: 180px;
    }

    .offers-left h1 {
        font-size: 18px;
    }

    .offers-left p {
        font-size: 11px;
    }

    .offers-left button {
        width: 110px;
        height: 30px; 
        font-size: 10px;
    }

    .offers-right {
        padding-top: 0px;
    }

    .offers-right img {
        width: auto;
        height: 300px;
        top: 50px;
    }
}

@media (max-width: 480px) {
    .offers {
        padding: 10px 20px;
        margin-bottom: 50px;
        height: 120px;
    }

    .offers-left h1 {
        font-size: 13px;
    }

    .offers-left p {
        font-size: 7px;
    }

    .offers-left button {
        width: 80px;
        height: 20px; 
        font-size: 6px;
    }

    .offers-right {
        padding-top: 0px;
    }

    .offers-right img {
        width: auto;
        height: 220px;
        top: 30px;
    }
}