.cartItems {
  margin: 100px 70px;
}

.cartItems hr {
  height: 3px;
  background: #e2e2e2;
  border: none;
}

.cartItems-format-main {
  display: grid;
  grid-template-columns: 0.5fr 2fr 1fr 1fr 1fr 1fr;
  align-items: center;
  gap: 75px;
  padding: 20px 0px;
  color: #454545;
  font-size: 18px;
  font-weight: 600;
}

.cartItems-format {
  font-size: 17px;
  font-weight: 500;
}

.cartIcon-product-icon {
  height: 70px;
}

.cartItems-remove-icon {
  width: 20px;
  margin: 0px 40px;
  cursor: pointer;
}

.cartItems-quantity {
  width: 64px;
  height: 50px;
  border: 2px solid #ebebeb;
  background: #fff;
}

.cartItems-down {
  display: flex;
  margin: 100px 0px;
}

.cartItems-total {
  flex: 1;
  display: flex;
  flex-direction: column;
  margin-right: 200px;
  gap: 40px;
}

.cartItems-total-item {
  display: flex;
  justify-content: space-between;
  padding: 15px 0px;
}

.cartItems-total-button {
  width: 262px;
  height: 58px;
  outline: none;
  border: none;
  background: #ff5a5a;
  color: #fff;
  font-weight: 600;
  cursor: pointer;
}

.cartItems-promo-code {
  flex: 1;
  font-size: 16px;
  font-weight: 500;
}

.cartItems-promo-code p {
  color: #555;
}

.cartItems-promo-box {
  width: 504px;
  margin-top: 15px;
  padding-left: 20px;
  height: 58px;
  background: #eaeaea;
}

.cartItems-promo-box input {
  border: none;
  outline: none;
  background: transparent;
  font-size: 16px;
  width: 330px;
  height: 50px;
}

.cartItems-promo-box button {
  width: 170px;
  height: 58px;
  font-size: 16px;
  background: black;
  color: white;
  cursor: pointer;
  border: none;
  outline: none;
}

@media (max-width: 1091px) {
  .cartItems {
    margin: 60px 60px;
  }

  .cartItems-format-main {
    grid-template-columns: 0.5fr 3fr 0.5fr 0.5fr 0.5fr 0.5fr;
    gap: 20px;
    padding: 15px 0;
    font-size: 15px;
  }

  .cartIcon-product-icon {
    height: 50px;
  }

  .cartItems-remove-icon {
    margin: auto;
  }

  .cartItems-quantity {
    width: 40px;
    height: 30px;
  }

  .cartItems-down {
    margin: 80px 0;
    flex-direction: column;
    gap: 80px;
  }

  .cartItems-total {
    margin: 0;
  }

  .cartItems-total-button {
    height: 45px;
  }

  .cartItems-promo-box {
    width: auto;
    max-width: 500px;
  }

  .cartItems-promo-box input {
    width: 100%;
  }

  .cartItems-promo-box button {
    width: 120px;
    margin-left: -125px;
  }
}

@media (max-width: 824px) {
  .cartItems {
    margin: 30px 20px;
  }

  .cartItems-format-main {
    grid-template-columns: 0.5fr 3fr 0.5fr 0.5fr 0.5fr 0.5fr;
    gap: 10px;
  }

  .headers {
    display: none;
    border: 2px solid blue;
  }
}

@media (max-width: 480px) {
  .cartItems-format-main {
    display: none;
    grid-template-columns: 0.5fr 3fr 0.5fr;
    gap: 10px;
  }

  .cartItems-format {
    display: grid;
  }
}
